.landing {
  font-family: 'Nunito', sans-serif;
  /* Animation */
  /*Shrinking for mobile*/
}

.landing .hero {
  padding: 80px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.landing .hero img {
  border-radius: 10px;
}

.landing .hero div .logo1 {
  width: 90%;
}

.landing .hero div h1 {
  font-family: 'Oswald', sans-serif;
  font-size: 72px;
  color: #9D99E6;
}

.landing .hero div h2 {
  font-size: 64px;
  color: #76C9BF;
  margin: 1rem;
  padding-bottom: 1rem;
}

.landing .hero div p {
  font-family: 'Open Sans', sans-serif;
  font-size: 28px;
  color: #3C5487;
}

.landing .hero div button {
  box-shadow: none;
  padding: 10px 30px;
  border: none;
  border-radius: 20px;
  background-image: linear-gradient(to right, #3A31E0, #9D99E6);
  color: white;
  font-size: 24px;
}

.landing .hero .landing1 {
  width: 50%;
  padding: 0 4rem 0 0;
}

.landing .hero .skills {
  width: 30%;
}

.landing .waves {
  position: relative;
  width: 100%;
  height: 18vh;
  margin-bottom: -7px;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}

.landing .parallax > use {
  -webkit-animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
          animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}

.landing .parallax > use:nth-child(1) {
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
}

.landing .parallax > use:nth-child(2) {
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}

.landing .parallax > use:nth-child(3) {
  -webkit-animation-delay: -4s;
          animation-delay: -4s;
  -webkit-animation-duration: 13s;
          animation-duration: 13s;
}

.landing .parallax > use:nth-child(4) {
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
  -webkit-animation-duration: 20s;
          animation-duration: 20s;
}

@-webkit-keyframes move-forever {
  0% {
    -webkit-transform: translate3d(-90px, 0, 0);
            transform: translate3d(-90px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(85px, 0, 0);
            transform: translate3d(85px, 0, 0);
  }
}

@keyframes move-forever {
  0% {
    -webkit-transform: translate3d(-90px, 0, 0);
            transform: translate3d(-90px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(85px, 0, 0);
            transform: translate3d(85px, 0, 0);
  }
}

@media (max-width: 768px) {
  .landing .waves {
    height: 40px;
    min-height: 40px;
  }
  .landing h1 {
    font-size: 24px;
  }
}

.landing .hero2 {
  padding: 80px 0;
}

.landing .hero2 h1 {
  font-family: 'Oswald', sans-serif;
  font-size: 48px;
