  color: white;
  font-size: 24px;
}

.landing .hero2 .blurbs {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.landing .hero2 .blurbs > * {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing .hero2 .blurbs > * > * {
  margin: 0.5rem;
}

.landing .hero2 .blurbs > * h2 {
  font-size: 36px;
  color: #3C5487;
  margin: 1rem;
  padding-bottom: 2rem;
}

.landing .hero2 .blurbs > * img {
  width: 70%;
}
