.posts {
  display: flex;
  flex-direction: column;
  margin-left: 40%;
}

.posts .createPost {
  border: lightgrey solid 2px;
  border-radius: 20px;
  color: gray;
  margin-bottom: 1rem;
  padding: 2rem 4rem;
  width: 60%;
}

.posts .displayPosts {
  display: flex;
  flex-direction: column-reverse;
}

.posts .displayPosts .post {
  border: lightgrey solid 2px;
  border-radius: 20px;
  margin-bottom: 1rem;
  padding: 1rem 4rem;
  width: 60%;
}

.posts .displayPosts .post .prof {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 1rem;
}

.posts .displayPosts .post .prof:first-child {
  align-items: center;
}

.posts .displayPosts .post .prof:first-child img {
  height: 125px;
  width: auto;
  border-radius: 50%;
  margin-right: 1rem;
}

.posts .displayPosts .post .prof:last-child {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.posts .displayPosts .post .tags {
  display: flex;
  flex-direction: row;
}

.posts .displayPosts .post .tags > * {
  border: lightgrey solid 2px;
  border-radius: 10px;
  padding: 0.2rem 0.4rem;
  margin-right: 0.2rem;
}

.posts .displayPosts .post img {
  border-radius: 10px;
  position: relative;
  width: 70%;
  max-height: 70%;
}
