.main {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

button {
  border: none;
  font-size: 1.5rem;
  padding: .4rem 1.5rem;
  background-color: #76c9bf;
  color: white;
  transition: 0.2s;
}

.button-hover {
  border-radius: 50px;
}

.button-hover:hover {
  background-color: #60afa6;
  color: #e0e0e0;
}

.addTagsButton {
  border-radius: 0 20px 20px 0;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: lightgrey solid 2px;
  border-radius: 10px;
  color: gray;
  margin: 2rem;
  padding: 2rem;
  max-width: 50vw;
}

.form > * {
  margin-bottom: 1.5rem;
}

.form .titleInput {
  display: flex;
  width: 100%;
  padding: .3rem 1rem;
  text-align: center;
  border: lightgray solid 2px;
  border-radius: 10px;
  font-size: 2rem;
  font-weight: 400;
}

.form textarea {
  width: 100%;
  min-height: 20vh;
  border: 1px lightgray solid;
  border-radius: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  text-align: center;
}

.form .img-input {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px lightgray solid;
  border-radius: 10px;
  padding: 2rem;
  width: 100%;
}

.form .tagSec {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form .tagSec h3 {
  margin-right: .5rem;
}

.form .tagSec > * {
  display: flex;
  flex-direction: row;
  margin-bottom: 1.5rem;
}

.form .tagSec .disTag {
  display: flex;
  flex-direction: row;
}

.form .tagSec .tag {
  display: flex;
  align-items: center;
  border: lightgrey solid 2px;
  border-radius: 10px;
  padding: 0 1rem;
  margin-right: 0.2rem;
}

.form .button-bottoms {
  display: flex;
  flex-direction: row;
  width: 50%;
  justify-content: space-around;
}
